.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  border-radius: 8px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  width: 90vw;
  height: 80vh;
  max-width: 1200px;
  max-height: 800px;
  min-width: 400px;
  min-height: 300px;
  overflow-y: auto;
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 0;
  border: none;
}

.window {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  border: none;
}

.window-header {
  background: linear-gradient(to right, #0078d7, #005a9e);
  color: white;
  padding: 10px 15px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  user-select: none;
  font-size: 14px;
  margin: 0;
}

.window-content {
  flex: 1;
  overflow: auto;
  padding: 0;
  margin: 0;
}

.workplaces-table {
  width: 100%;
  border-collapse: collapse;
  padding: 0;
  margin: 0;
}

.table-header {
  display: flex;
  background-color: #f8f9fa;
  font-weight: bold;
  border-bottom: 1px solid #dee2e6;
  padding: 14px 12px;
  font-size: 10px;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
}

.table-row {
  display: flex;
  border-bottom: 1px solid #dee2e6;
  padding: 14px 12px;
  transition: background-color 0.2s;
  width: 100%;
  box-sizing: border-box;
  margin: 0;
}

.table-row:hover {
  background-color: #f0f8ff;
}

.table-cell {
  flex: 1;
  padding: 8px 4px;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px; /* Increased font size for better readability */
  line-height: 1.1;
  min-width: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.table-cell:first-child {
  flex: 2;
  font-weight: 500;
}

.status-badge {
  padding: 3px 6px;
  border-radius: 2px;
  font-size: 10px; /* Increased font size for better readability */
  font-weight: 500;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.status-atama {
  background-color: #bbdefb;
  color: #0d47a1;
}

.status-bekliyor {
  background-color: #fff9c4;
  color: #7f6000;
}

.status-onaylandi {
  background-color: #c8e6c9;
  color: #1b5e20;
}

.btn {
  padding: 4px 8px;
  border: 1px solid #ccc;
  border-radius: 3px;
  background: #f8f7f7;
  font-family: 'Segoe UI', 'Tahoma', sans-serif;
  font-weight: 500;
  cursor: pointer;
  font-size: 10px;
  transition: all 0.2s ease;
  height: 20px;
}

.btn:hover {
  background: #e0e0e0;
  border-color: #aaa8a8;
}

.btn:active {
  background: #d0d0d0;
}

.btn-primary {
  background: #0078d7;
  color: white;
  border-color: #005a9e;
}

.btn-primary:hover {
  background: #0066b4;
  border-color: #004578;
}

.btn-primary:active {
  background: #005a9e;
  border-color: #003a66;
}

.btn-close {
  background: #14af0f;
  color: black;
  border-color: #080808;
  padding: 2px 8px;
  font-size: 10px;
  border-radius: 3px;
  min-width: 40px;
  text-align: center;
  height: 20px;
}

.btn-close:hover {
  background: #5a6268;
  border-color: #484e53;
  color: black;
}

.btn-close:focus {
  outline: 2px solid #8a8d93;
  outline-offset: 2px;
}

.btn-danger {
  background: #d32f2f;
  color: white;
  border-color: #b71c1c;
}

.btn-danger:hover {
  background: #b71c1c;
  border-color: #8b0000;
}

.btn-danger:active {
  background: #8b0000;
  border-color: #600000;
}

.modal-actions {
  padding: 15px;
  border-top: 1px solid #eee;
  background: white;
  display: flex;
  justify-content: flex-end;
  margin: 0;
}